.confirm-button {
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    font-size: 1.3rem;
    font-weight: 600;
    padding: 0.8rem 3rem;
    color: black;
    background-color: white;
    text-transform: uppercase;

    &:hover {
        box-shadow: 1px 1px 25px 10px lightyellow;
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(120deg,
                transparent,
                lightyellow,
                transparent);
        transition: all 650ms;
    }

    &:hover:before {
        left: 100%;
    }

    &:active {
        background-color: lightsalmon;
        
        &::before,
        &::after {
            background-color: lightsalmon;
        }
    }
}